





































































	export default {
		data() {
			return {
				quota: {
          can_withdraw: false,
          balance: 0,
          amount_limit: 0,
          time_limit: 0
        },
        quotaing: true,
        balance: 0,
        hasRes: false,
        money: '',
        err: 0,
        choiceArr: [
          {
            num: 1,
            optional: false
          },
          {
            num: 10,
            optional: false
          },
          {
            num: 30,
            optional: false
          }
        ],
        choiceIndex: -1,
        choiceTimer: null,
        withdrawing: false,
        msg: '请稍后至微信钱包查看',
        success: 0 // 提现状态，0失败，1成功，2等待
			};
		},
    computed: {
      config() {
        return this.$store.state.config
      },
      constant() {
        return this.$constant
      },
      userInfo() {
        return this.$store.state.userInfo
      },
      isRole() {
        return this.userInfo.role > 0
      },
      isAgent() {
        return this.userInfo.role > 1
      },
      isDisabled() {
        return this.money.length <= 0 || this.err !== 0 || (this.isAgent && (this.quotaing || !this.quota.can_withdraw))
      },
      stateClass() {
        if (this.success === 1) {
          return 'icon-wait'
        } else if (this.success === 2) {
          return 'icon-wait'
        } else if (this.success === 3) {
          return 'icon-suc'
        } else {
          return 'icon-err'
        }
      },
      tipsTitle() {
        if (this.success === 1) {
          return '申请已提交'
        } else if (this.success === 2) {
          return '提交中'
        } else if (this.success === 3) {
          return '提现成功'
        } else {
          return '申请提现失败'
        }
      },
      isSubscribe() {
        return this.userInfo.is_subscribe === 1
      },
      canWithdrawNum() {
        if (this.isAgent) {
          return Math.min(parseFloat(this.userInfo.balance), parseFloat(this.quota.amount_limit), parseFloat(this.quota.amount_limit))
        } else {
          return parseFloat(this.userInfo.balance)
        }
      }
    },
		onLoad() {
      this.balance = this.userInfo.balance
      // 检查是否可以提现
      if (!this.isAgent) {
        this.quotaing = false
        this.quota.can_withdraw = true
        let arr = []
        this.choiceArr.forEach((item) => {
          arr.push(item.num)
        })
        this.$http({
          url: this.$apis.checkWithdraw,
          method: 'POST',
          data: {
            amount: arr
          },
          cb: (err, data) => {
            if (!err && data.success) {
              this.choiceArr.forEach((item) => {
                if (data.data.indexOf(item.num) !== -1) {
                  item.optional = true
                }
              })
            } else {
              uni.showToast({
              	title: '服务器繁忙，请稍后再试',
                icon: 'none'
              })
            }
          }
        })
      } else {
        // 检查今天还可以提现的额度
        this.$http({
          url: this.$apis.withdrawQuota,
          method: 'GET',
          data: null,
          cb: (err, data) => {
            if (!err && data.success) {
              this.quota = data.data
            } else {
              uni.showToast({
              	title: '服务器繁忙，请稍后再试',
                icon: 'none'
              })
            }
            this.quotaing = false
          }
        })
      }
		},
    methods: {
      choiceNum(index) {
        clearTimeout(this.choiceTimer)
        if (this.choiceIndex === index) return
        this.err = 0
        this.choiceIndex = index
        if (!this.choiceArr[index].optional) {
          this.money = ''
          this.choiceTimer = setTimeout(() => {
            this.err = 3
          }, 16.67)
          return
        }
        let num = this.choiceArr[index].num
        this.money = num
      },
      withdraw() {
        if (this.isDisabled) return
        if (!this.userInfo.is_bind_wx) {
          uni.showModal({
          	title: '提示',
          	content: '提现功能要绑定微信或者支付宝',
          	cancelText: '取消',
          	confirmText: '去绑定',
          	success: res => {
              if (res.confirm) {
                this.$navigateTo({
                  url: '/pages/mine/account'
                })
              }
            }
          });
          return
        }
        // 提现
        let _this = this
        _this.hasRes = true
        _this.success = 2
        _this.msg = '申请提交中，请耐心等待'
        _this.title = '提交中'
        _this.$http({
          url: this.$apis.withdraw,
          method: 'POST',
          data: {
            amount: _this.money
          },
          cb: (err, data) => {
            if (!err && data.success) {
              if (_this.isSubscribe) {
                _this.success = 3
                _this.msg = '请稍后至微信钱包查看'
                _this.title = '提现成功'
                _this.withdrawing = true
              } else {
                _this.success = 1
                _this.msg = '请留意您的客服信息'
                _this.title = '申请已提交'
                _this.withdrawing = true
              }
              _this.$getInfo(() => {
                _this.balance = _this.userInfo.balance
              })
            } else {
              if (data.data && data.data.is_subscribe === false) {
                _this.success = 1
                _this.msg = '请留意您的客服信息'
                _this.title = '申请已提交'
                _this.withdrawing = true
              } else {
                _this.success = 0
                _this.msg = data.message
                _this.title = '申请提现失败'
                _this.withdrawing = false
              }
            }
          }
        })
      },
      gotoEarning() {
        this.$navigateTo({
          url: '/pages/mine/earning?tab=2'
        })
      }
    },
    watch: {
      money(val) {
        if ((!/^\d{0,3}?$/g.test(val) && val !== '')) {
          this.err = 1
        } else if (parseFloat(val) > this.canWithdrawNum) {
          this.err = 2
        } else {
          this.err = 0
        }
      }
    }
	}
